.container {
  width: 90%;
  margin: 1rem auto;
  background-color: var(--background-content);
  border-radius: var(--boder-radius-large);
  user-select: none;
  padding: 1rem;

  .user {
    .img {
      width: 10rem;
      height: 10rem;
      border-radius: 50%;
      object-fit: cover;
      box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
    }

    .name {
      font-size: 1.25rem;
      font-weight: bolder;
      color: var(--black-color);
      text-transform: uppercase;
      margin: .5rem 0;
    }

    .major {
      color: var(--black-color);
      font-weight: 700;
      font-size: .8rem;
      text-transform: uppercase;
      margin: .25rem 0 .75rem;
    }

    .quote {
      color: var(--color-quote);
      font-size: 2rem;
    }

    .des::first-letter {
      text-transform: capitalize;
    }

    .des {
      font-size: .8rem;
      color: var(--black-color);
      font-weight: 600;
      line-height: 1.5rem;
      width: 90%;
      text-align: left;
    }
  }

  .aboutMe {
    background-color: var(--background-cv);
    border-radius: var(--boder-radius-large);
    padding: 1rem 0;
  }

  .infors {
    position: relative;

    .icon {
      position: absolute;
      left: -1.5rem;
      background-color: var(--color-cv);
      display: inline-flex;
      flex-direction: column;
      color: var(--white-color);
      gap: 1.75rem;
      font-size: 1.15rem;
      width: 2.75rem;
      padding: .75rem 0;
      border-radius: 2rem;
    }

    .infor {
      padding: .5rem 2rem 0;
      display: flex;
      flex-direction: column;
      gap: .5rem;

      .heading::first-letter {
        text-transform: capitalize;
      }

      .heading {
        color: var(--black-color);
        font-size: .85rem;
        font-weight: bolder;
      }

      .des {
        color: var(--black-color);
        font-weight: 500;
        font-size: .85rem;
        padding-top: .15rem;
        text-transform: capitalize;
      }

      .desEmail {
        text-transform: lowercase;
      }

    }
  }

  .educationSkill {

    .educations,
    .skills,
    .cvs {
      position: relative;
    }

    .education,
    .skill,
    .cv {
      padding: .5rem 2rem 0;
    }

    .icon {
      position: absolute;
      left: -1.5rem;
      top: 1rem;
      background-color: var(--color-cv);
      color: var(--white-color);
      font-size: 1.15rem;
      border-radius: 50%;
      width: 2.75rem;
      height: 2.75rem;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .title {
      color: var(--black-color);
      font-size: .9rem;
      font-weight: bolder;
      text-transform: uppercase;
      padding: 1.25rem 0 .75rem;
    }

    .heading,
    .des {
      color: var(--black-color);
      text-transform: capitalize;
      font-size: .85rem;
    }

    .heading {
      font-weight: 900;
    }

    .des {
      font-weight: 500;
      padding-left: .25rem;
    }

    .myCV {
      color: var(--color-facebook);
      font-size: .9rem;

      &:hover {
        font-weight: 900;
      }
    }
  }

}


@media (max-width : 575px) {
  .container {
    .infors {
      .icon {
        left: -0.5rem;
        width: 2.25rem;
        font-size: 1.1rem;
      }

      .infor {
        padding-left: 2.5rem;
        padding-top: 0;

        .des {
          display: flex;
          flex-wrap: wrap;
        }
      }
    }

    .educationSkill {
      .title{
        padding-bottom: .5rem;
      }
      .education,
      .skill,
      .cv {
        padding: 0 2.5rem;
      }

      .icon{
        left: -0.5rem;
        width: 2.25rem;
        height: 2.25rem;
        font-size: 1.1rem;

      }
    }
  }
}

@media (min-width : 576px) and (max-width :768px) {
  .container {
    .infors {
      .icon {
        left: -0.5rem;
      }

      .infor {
        padding-left: 3rem;
        padding-top: 0;

        .des {
          display: flex;
          flex-wrap: wrap;
        }
      }
    }

    .educationSkill {
      .title{
        padding-bottom: .5rem;
      }
      .education,
      .skill,
      .cv {
        padding: 0 3rem;
      }

      .icon{
        left: -0.5rem;

      }
    }
  }
}

@media (min-width : 769px) and (max-width :991px) {}